class StoryList extends Component {
  desc(el) {
      let txt = el.description.substring(0, 350) + "..."
      return txt
  }
  render() {
    return (<>
        {data.map((el, i)=>{
            return <StoryItem key={i} id={el.id} heading={el.heading} tags={el.tags} description={this.desc(el)} date={el.date} views={el.views}/>
        })}
        </>
    );
  }
}

export default StoryList;
